body {
  margin-top: 50px; 
  background-color: black;
}
.navbar {
  background: linear-gradient(to right, red, #d8de1a, green); 
}

.navbar-brand {
  color: rgb(8, 7, 7); 
}

.navbar-toggler-icon {
  background-color: rgb(52, 51, 51); 
}


.nav-link,
.dropdown-item {
  color: white !important; 
}

.nav-item.dropdown:hover .nav-link,
.nav-item.dropdown:hover .dropdown-item {
  background: linear-gradient(to right, red, #d8de1a, green); 
}

.dropdown-menu {
  background: linear-gradient(to right, red, #d8de1a, green); 
}

.dropdown-item:hover {
  background: linear-gradient(to right, red, #d8de1a, green); 
}
.container {
  display: flex;
  justify-content: space-between;
  max-width: 1500px;
  margin: 0 auto;
  padding: 20px;
}

.column-left {
  width: 49%;
  background-color: #fcfffb;
  padding: 20px;
}

.column-right {
  width: 49%;
  background-color: #fcfffb;
  padding: 20px;
}
.foder {
  background-color: #0f0202; 
  color: white !important;
  padding: 10px;
  margin-top: 20px;
  text-align: center; 
}

.carousel-inner img {
  width: 100%; 
  height: 350px; 
}

/* Estilos adicionales para ajustar el tamaño del carousel */
.carousel {
  max-width: 800px; 
  margin: 0 auto; 
}


