.player-wrapper {
  position: relative;
  padding-top: 40%; 
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}

.formulario-division {
  width: 400px;
  padding: 20px;
  border-radius: 10px;
  background: white;
  text-align: center;
  margin: 0 auto; /* Esto centrará el elemento horizontalmente */
  margin-top: 150px; /* Puedes ajustar el margen superior según tus necesidades */
}



/* Estilo para el botón de Enviar */
